import React from 'react';
import { createRoot } from 'react-dom/client';
import Popup from './Popup';
import './globals.css';
import { persister, queryClient } from '../../react-query/queryClient';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';
import { PersistQueryClientProvider } from '@tanstack/react-query-persist-client';
import { getActiveTab } from '../../utils/chrome-utils';
import {
  CHROME_STORAGE_ID,
  MESSAGE_TOPIC,
} from '../../axiosInstance/constants';
import { chromeStorageGetter } from '../../ts-cookie/hooks/setCookie';
import { getDomain } from '../../utils/getDomain';
import { useStoreSearch } from '../../zustand/useStoreList';
import { useStoreData } from '../../zustand/useStoreData';
import { decompressFromUTF16 } from 'lz-string';
import { getUserData } from '../../utils/getUserData';
import { useUserStore } from '../../zustand/useUser';
import { REFERMATE_TUTORIAL } from '../../utils/constants/app';

const { domain } = getDomain();
const CURRENT_STORE_DETAILS_KEY = `${CHROME_STORAGE_ID.REFERMATE_STOREDETAILS}${domain}`;

if (process.env.NODE_ENV === 'production') {
  console.log = () => {};
  console.error = () => {};
  console.debug = () => {};
}

chrome.storage.onChanged.addListener(async (changes) => {
  console.log('Changes from Popup/index.jsx %o', changes);

  // Listen for store details changes.
  if (changes[CURRENT_STORE_DETAILS_KEY]) {
    const newValue = changes[CURRENT_STORE_DETAILS_KEY].newValue;
    const oldValue = changes[CURRENT_STORE_DETAILS_KEY].oldValue;
    const refermateStoreDetails = changes[CURRENT_STORE_DETAILS_KEY];

    if (newValue && oldValue) {
      if (JSON.parse(refermateStoreDetails?.newValue)?.value) {
        useStoreData
          .getState()
          .actions.setStoreData(
            JSON.parse(JSON.parse(refermateStoreDetails.newValue)?.value)
          );
      }
    }
  }

  // Listen for entire store list changes.

  if (changes[CHROME_STORAGE_ID.REFERMATE_ENTIRE_STORE]) {
    const refermateStoresList =
      changes[CHROME_STORAGE_ID.REFERMATE_ENTIRE_STORE];
    const encryptedData = JSON.parse(
      refermateStoresList.hasOwnProperty('oldValue')
        ? refermateStoresList.oldValue
        : refermateStoresList.newValue
    )?.value;
    const parsedEncryptedData = decompressFromUTF16(encryptedData);

    // if old data existed, but new data is now undefined = no new data = no store object = cleared recently
    if (refermateStoresList.oldValue && !refermateStoresList.newValue) {
      useStoreSearch
        .getState()
        .actions.setStore(JSON.parse(parsedEncryptedData));
    } else {
      useStoreSearch
        .getState()
        .actions.setStore(JSON.parse(parsedEncryptedData));
    }
  }

  // Listen for user changes.
  if (changes[CHROME_STORAGE_ID.REFERMATE_USER]) {
    const refermateUser = changes[CHROME_STORAGE_ID.REFERMATE_USER];

    // if new data exists, but old data is now undefined = new fresh data = new user object = signed in recently
    if (!refermateUser.oldValue && refermateUser.newValue) {
      useUserStore
        .getState()
        .actions.setUser(
          JSON.parse(JSON.parse(refermateUser.newValue)?.value).user
        );
    }
  }
});

const loadCurrentStore = async (domain) => {
  const storeData = await chromeStorageGetter(
    CHROME_STORAGE_ID.REFERMATE_STOREDETAILS,
    domain
  );
  if (storeData) {
    localStorage.setItem(
      `${CHROME_STORAGE_ID.REFERMATE_STOREDETAILS}${domain}`,
      storeData
    );
    useStoreData.getState().actions.setStoreData(JSON.parse(storeData)); // Set the StoreData
  }
};

const loadAllStores = async () => {
  const allStoresData = await chromeStorageGetter(
    CHROME_STORAGE_ID.REFERMATE_ENTIRE_STORE,
    '',
    async () => {
      // Fetch the entire store
      // await getAllStores();
      await chrome.runtime.sendMessage({
        type: MESSAGE_TOPIC.FETCH_ALL_STORES,
      });
    }
  );
  if (allStoresData) {
    // Set the Zustand store when available, uses a Promise to not block main thread.
    new Promise(() => {
      useStoreSearch
        .getState()
        .actions.setStore(JSON.parse(decompressFromUTF16(allStoresData)));
    });
  }
};

getActiveTab().then(async (tab) => {
  await loadAllStores();
  await loadCurrentStore(tab.url);
  await getUserData('Popup', 'index.jsx');
});

const container = document.getElementById('app-container');
const root = createRoot(container); // createRoot(container!) if you use TypeScript
chrome.storage.local.get([REFERMATE_TUTORIAL.STORAGE_KEY]).then((result) => {
  root.render(
    <PersistQueryClientProvider
      client={queryClient}
      persistOptions={{ persister }}
    >
      <Popup tutorials={result.refermateTutorial || []} />
      <ReactQueryDevtools />
    </PersistQueryClientProvider>
  );
});
