export var getDomain = function (url) {
    var domain = url || window.location.hostname;
    var address = url || window.location.href;
    var isDeveloperPage = address.includes('developer.');
    var isAppPage = address.includes('app.');
    var isRefermate = address.includes('refermate.');
    if (isDeveloperPage || isAppPage || isRefermate) {
        domain = null;
    }
    else {
        domain = domain
            .replace(/^cart./, '')
            .replace('http://', '')
            .replace('https://', '')
            .replace('www.', '')
            .replace('www2.', '')
            .split(/[/?#]/)[0];
    }
    return { domain: domain };
};
