import { baseUrl } from '../axiosInstance/constants';
import { MESSAGE_TOPIC } from '../axiosInstance/constants';
import { isSafariMobile } from './systemInfoService';
var targetBrowser = process.env.TARGET_BROWSER;
export var openAffiliateURL = function (storeId, tabId) {
    if (isSafariMobile)
        targetBrowser = 'mobile';
    chrome.runtime.sendMessage({
        type: MESSAGE_TOPIC.OPEN_AFFILIATE_URL,
        data: {
            affiliateUrl: "".concat(baseUrl, "/stores/").concat(storeId, "/activate?source=").concat(targetBrowser),
            tabId: tabId,
        },
    }, function (response) {
        console.log(response);
    });
};
